import React, { ReactNode } from "react";
import styled from "styled-components";

import IconButton from "../Button/IconButton";
import { Row } from "../Grid";

import { useDrawerContext } from "./DrawerContext";

type DrawerTitleProps = {
	children?: ReactNode
	className?: string
};

const CloseButton = styled(IconButton)`
	position: absolute;
	right: 0;
	background: transparent;
`;

const DrawerTitle = ({
	children,
	className,
}: DrawerTitleProps) => {
	const { onRequestClose, hideCloseButton } = useDrawerContext();

	return (
		<Row
			wrap="nowrap"
			align="center"
			className={className}
			// using style prop so that margin takes precedence
			style={{
				margin: 0,
				position: "relative",
			}}
		>
			{children}
			{
				hideCloseButton ? null : (
					<CloseButton
						icon="CircleX"
						variant="tertiary"
						dataQA="drawer-close-button"
						onClick={onRequestClose}
						aria-label="close drawer"
					/>
				)
			}
		</Row>
	);
};

export default DrawerTitle;
